
import CheckBox from 'component/CheckBox';
import { useState } from 'react';
import styles from './index.less'
const options = [
    { key: 1, content: '第一个选项', className: styles.checkbox },
    { key: 2, content: '第二个选项', className: styles.checkbox },
    {
        key: 3, content: '禁用的未选项', disabled: true, className: styles.checkbox,
    },
    {
        key: 4, content: '禁用的已选项', disabled: true, className: styles.checkbox,
    },
    {
        key: 5, content: '半选状态', indeterminate: true, className: styles.checkbox,
    },
    {
        key: 6, content: '禁用半选状态', indeterminate: true, disabled: true, className: styles.checkbox,
    },
]
const CheckBoxCircleView = () => {
    const [selectedKeys, setSelectedKeys] = useState<number[]>([1, 2]);
    return (
        <div>
            <CheckBox
                options={options}
                checkedKeys={selectedKeys}
                onChange={setSelectedKeys}
                circle
            ></CheckBox>
        </div>
    )
}

export default CheckBoxCircleView