

import styles from './index.less';
import CheckIcon from './CHeckIcon';
import classNames from 'classnames';
import { useState } from 'react';
import { concat, filter } from 'lodash';


// 样式按照一线级别来排列 

interface Props {
    options: any,
    checkedKeys?: any,
    onChange?: (arr: any) => void;
    circle?: boolean;
    style?: any;
    checked?: boolean,
    indeterminate?: boolean,
    disabled?: boolean,
    onClick?: any,
}
const CheckBox = (props: Props) => {
    const {
        options, checkedKeys = [],
        onChange, circle, style,
        checked, indeterminate, disabled,
        onClick,
    } = props;
    const handleChange = (isChecked: any, key: any) => {
        if (onChange === undefined) {
            return;
        }
        if (isChecked) {
            onChange(filter(checkedKeys, (item) => item !== key));
        } else {
            onChange(concat(checkedKeys, key));
        }
    }
    if (!options) {
        return (<label

            className={classNames(styles.checkboxcontainer, {
                [styles.checkContainer]: checked,
                [styles.banContainer]: indeterminate,
                [styles.disableContainer]: disabled,
                [styles.circle]: circle,
                // [styles.horizontal]: true,
            })} style={style}  onClick={onClick}>
            {/* <input
                type="checkbox" name="" id=""
                className={styles.checkboxInput}
                disabled={disabled}
                checked={checked}
                onChange={() => {
                    if (onChange) {
                        onChange(!checked)
                    }

                }}
               
            /> */}
            <span className={styles.sequre} style={{margin: 0}}>
                <CheckIcon className={styles.svg} />
            </span>
        </label>
        )
    }
    return (
        <>
            {
                options.map((item: any) => (
                    <label
                        key={item.key}
                        className={classNames(styles.checkboxcontainer, {
                            [styles.checkContainer]: checkedKeys.includes(item.key),
                            [styles.banContainer]: item.indeterminate,
                            [styles.disableContainer]: item.disabled,
                            [styles.circle]: circle,
                            [styles.horizontal]: true,
                        })} style={style}>
                        <input
                            type="checkbox" name="" id=""
                            className={styles.checkboxInput}
                            disabled={item.disabled}
                            checked={checkedKeys.includes(item.key)}
                            onChange={() => handleChange(checkedKeys.includes(item.key), item.key)}
                        />
                        <span className={styles.sequre}>
                            <CheckIcon className={styles.svg} />
                        </span>
                        <span>{item.content}</span>
                    </label>

                ))
            }


        </>
    )
}

export default CheckBox;